.Button {
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    font: inherit;
    font-weight: bold;
    color: white;
    background-color: rgb(var(--baseClr));
    padding: .5rem;
    margin: .5rem;
    border-radius: .15rem;
}

.Button:hover {
    opacity: .9;
}

.Button:disabled {
    background-color: gray;
    cursor: not-allowed;
    opacity: .8;
}

.Success {
    background-color: green;
}

.Danger {
    background-color: red;
}

