.Input {
    width: 100%;
    padding: .5rem;
}

.Label {
    font-weight: bold;
    display: block;
    margin-bottom: .5rem;
}

.InputElement {
    outline: none;
    border: 1px solid rgba(var(--baseClr), 0.6);
    background-color: white;
    font: inherit;
    padding: .3rem .6rem;
    display: block;
    width: 100%;
}

.InputElement:focus {
    outline: none;
}

.Invalid {
    border: 1px solid black;
    background-color: salmon;
}

.ValidationError {
    color: red;
    margin: .25rem 0;
    font-size: .6rem;
} 

.MessageContainer {
    position: relative;
}

.Textarea {
    min-height: 10rem;
}

.MessageLength {
    position: absolute;
    right: 0;
    bottom: -1.2rem;
    font-size: .7rem;
}