.NewPost {
    margin: 0 auto;
    padding: 8rem 0 4rem 0;
    width: 35rem;
    max-width: 90vw;
}

.FileInput {
    width: calc(100% - 1rem);
    max-height: 27rem;
    height: 67.5vw;;
    margin: .5rem;
    background: url('../../assets/images/plus.svg') center center / contain no-repeat;
    background-size: 4rem;
    background-color:#ddd;
}

.FileInput > input {
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
}